import { useState, useEffect } from "react";

export const Testimonials = (props) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const testimonials = [
    {
      text: "   ,        VRF 6 .            ,   ,   .    5      .     .",
      name: "  -  ",
    },
    {
      text: "     VRF  .                 .               .         .",
      name: "  -  ",
    },
    {
      text: "  , ,   .    VRF   5 . +  wifi    .     !  .",
      name: "  - ",
    },
    {
      text: "       ,     VRF    6 ,            ,      ,   !! !    !! !!",
      name: "  -  ",
    },
    {
      text: ", ,   .         .",
      name: "  - ",
    },
    {
      text: "  .  . ",
      name: " -  ",
    },
    {
      text: "   .            .      ...      ,     .          .",
      name: "  - ",
    },
    {
      text: "   ...   ,        .   ,    ,  .   , ,   ... ,     ,   .... ,  ...        ,    .",
      name: " - ",
    },
    {
      text: "I had a great experience with YABAT AC! They installed my air conditioning system quickly and professionally. The work was neat, and everything functions perfectly. Highly recommend!",
      name: "Dima",
    },
    {
      text: "   ,        .   ,    ,           .     ,  ,          .",
      name: " - ",
    },
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prevIndex) =>
        prevIndex === testimonials.length - 1 ? 0 : prevIndex + 1
      );
    }, 5000); // Change testimonial every 5 seconds

    return () => clearInterval(interval);
  }, [testimonials.length]);

  const nextTestimonial = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === testimonials.length - 1 ? 0 : prevIndex + 1
    );
  };

  const prevTestimonial = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex === 0 ? testimonials.length - 1 : prevIndex - 1
    );
  };

  return (
    <div id="testimonials">
      <div className="container">
        <div className="section-title text-center">
          <h2>  </h2>
          <p>    </p>
        </div>
        <div className="testimonials-container">
          <div className="testimonial-slider">
            <button
              className="testimonial-nav prev"
              onClick={prevTestimonial}
              aria-label=" "
            >
              
            </button>

            <div className="testimonial-content">
              <div className="testimonial-text">
                <p>"{testimonials[currentIndex].text}"</p>
              </div>
              <div className="testimonial-meta">
                <strong>{testimonials[currentIndex].name}</strong>
              </div>
            </div>

            <button
              className="testimonial-nav next"
              onClick={nextTestimonial}
              aria-label=" "
            >
              
            </button>
          </div>

          <div className="testimonial-dots">
            {testimonials.map((_, index) => (
              <button
                key={index}
                className={`dot ${index === currentIndex ? "active" : ""}`}
                onClick={() => setCurrentIndex(index)}
                aria-label={`  ${index + 1}`}
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};
